<template>
    <div>
    <el-table  :height="500"
            :data="tableData.filter(data => !search || data.name.toLowerCase().includes(search.toLowerCase()))"
            style="width: 100%">
        <el-table-column
                label="id"
                width="180">
            <template slot-scope="scope">
                <i class="el-icon-time"></i>
                <span style="margin-left: 10px">{{ scope.row.id }}</span>
            </template>
        </el-table-column>
        <el-table-column
                label="姓名"
                width="180">
            <template slot-scope="scope">
                <el-popover trigger="hover" placement="top">
                    <p>姓名: {{ scope.row.name }}</p>
                    <p>住址: {{ scope.row.address }}</p>
                    <div slot="reference" class="name-wrapper">
                        <el-tag size="medium">{{ scope.row.name }}</el-tag>
                    </div>
                </el-popover>
            </template>
        </el-table-column>
        <el-table-column
                label="生日"
                width="180">
            <template slot-scope="scope">
                <i class="el-icon-time"></i>
                <span style="margin-left: 10px">{{ scope.row.bri }}</span>
            </template>
        </el-table-column>
        <el-table-column
                label="性别"
                width="180">
            <template slot-scope="scope">
                <span style="margin-left: 10px">{{ scope.row.sex }}</span>
            </template>
        </el-table-column>

        <el-table-column
                label="地址"
                width="180">
            <template slot-scope="scope">
                <span style="margin-left: 10px">{{ scope.row.address }}</span>
            </template>
        </el-table-column>



        <el-table-column align="right">
            <template slot="header" slot-scope="scope">
                <el-input
                        v-model="search"
                        size="mini"
                        placeholder="输入姓名关键字搜索"/>
            </template>

            <template slot-scope="scope">
                <el-button
                        size="mini"
                        @click="handleEdit(scope.$index, scope.row)">编辑</el-button>

                <el-popconfirm
                        confirmButtonText='好的'
                        cancelButtonText='不用了'
                        icon="el-icon-info"
                        iconColor="red"
                        title="这是确定删除当前用户嘛？"
                        @onConfirm="handleDelete(scope.$index, scope.row)"

                >
                    <el-button slot="reference"  size="mini"
                               type="danger"
                               >删除</el-button>
                </el-popconfirm>
            </template>
        </el-table-column>
    </el-table>
        <el-pagination
                style="margin: 15px 0px"
                layout="prev, pager, next,jumper,total,sizes"
                :page-size="size"
                :current-page="pageNow"
                :page-sizes="[2,4,6,8,10]"
                @current-change="findPage"
                @size-change="findSize"
                :total="total">
        </el-pagination>

        <el-dialog title="修改用户信息" :visible.sync="dialogFormVisible">
            <el-form :model="form">
                <el-form-item label="姓名">
                    <el-input v-model="form.name"></el-input>
                </el-form-item>

                <el-form-item label="生日">
                    <el-date-picker  type="date" placeholder="选择日期" v-model="form.bri" style="width: 100%;"></el-date-picker>
                </el-form-item>

                <el-form-item label="性别">
                    <el-radio-group v-model="form.sex">
                        <el-radio label="男"></el-radio>
                        <el-radio label="女"></el-radio>
                    </el-radio-group>
                </el-form-item>

                <el-form-item label="地址">
                    <el-input type="textarea" v-model="form.address"></el-input>
                </el-form-item>

            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="dialogFormVisible = false">取 消</el-button>
                <el-button type="primary" @click="updataUser">确 定</el-button>
            </div>
        </el-dialog>

        <el-button type="success" @click="show = !show" style="margin:10px 0px;" size="meduim">添加</el-button>
        <transition name="el-zoom-in-center">
            <div v-show="show" class="transition-box">
                <el-form  hide-required-asterisk  :rules="rules" ref="userForm" :model="form" label-width="80px">
                    <el-form-item label="姓名" prop="name">
                        <el-input v-model="form.name"></el-input>
                    </el-form-item>

                    <el-form-item label="生日" prop="birth">
                        <el-date-picker value-format="yyyy-MM-dd" type="date" placeholder="选择日期" v-model="form.bri" style="width: 100%;"></el-date-picker>
                    </el-form-item>

                    <el-form-item label="性别">
                        <el-radio-group v-model="form.sex">
                            <el-radio label="男"></el-radio>
                            <el-radio label="女"></el-radio>
                        </el-radio-group>
                    </el-form-item>

                    <el-form-item label="地址" prop="address">
                        <el-input type="textarea" v-model="form.address"></el-input>
                    </el-form-item>
                    <el-form-item>
                        <el-button type="primary" @click="onSubmit('userForm')">保存用户</el-button>
                        <el-button>取消</el-button>
                    </el-form-item>
                </el-form>
            </div>
        </transition>


        <!-- Form -->
<!--        <el-button type="text" @click="dialogFormVisible = true">打开嵌套表单的 Dialog</el-button>-->

        <el-dialog title="修改用户信息" :visible.sync="dialogFormVisible">
            <el-form :model="form">
                <el-form-item label="姓名">
                    <el-input v-model="form.name"></el-input>
                </el-form-item>

                <el-form-item label="生日">
                    <el-date-picker type="date" placeholder="选择日期" v-model="form.bri" style="width: 100%;"></el-date-picker>
                </el-form-item>

                <el-form-item label="性别">
                    <el-radio-group v-model="form.sex">
                        <el-radio label="男"></el-radio>
                        <el-radio label="女"></el-radio>
                    </el-radio-group>
                </el-form-item>

                <el-form-item label="地址">
                    <el-input type="textarea" v-model="form.address"></el-input>
                </el-form-item>

            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="dialogFormVisible = false">取 消</el-button>
                <el-button type="primary" @click="updataUser">确 定</el-button>
            </div>
        </el-dialog>


        </div>
</template>


<script>
    export default {
        name: "List",

        data() {
            return {
                tableData: [],
                search:'',
                show: 'false',
                form: {
                    name: '',
                    bri: '',
                    sex: '女',
                    address: ''
                },
                total: 0,
                size: 4,
                pageNow: 1,
                rules: {
                    name: [
                        { required: true, message: '请输入用户名', trigger: 'blur' },
                    ],
                    // birth: [
                    //     { required: true, message: '请输入生日', trigger: 'change' },
                    // ],
                    address: [
                        { required: true, message: '请输入地址', trigger: 'blur' },
                    ],
                },
                dialogTableVisible: false,
                dialogFormVisible: false,
            }


        },
        methods: {

            findSize(size){
                console.log(size);
                this.size=size;
                this.findALL(this.page,size);
            },
            findPage(page){//用于处理分页
                console.log(page);
                this.page=page;
                this.findALL(page,this.size);

            },
            updataUser(){
                axios.post("http://localhost:8888/user/updata",this.form).then(res=>{


                    // this.tableData=res.data;
                    console.log(res.data);
                    if(res.data.status){
                        this.$message({
                            message: '恭喜你'+res.data.msg,
                            type: 'success'
                        });
                        this.form={};
                        this.show=false;
                        this.findALL();
                    }else {
                        this.$message.error(res.data.msg);
                    }
                })
                this.dialogFormVisible = false
                this.findALL();
            },
            handleEdit(index, row) {
                // console.log(index, row);
                this.show=true;
                this.form=row;
                this.dialogFormVisible=true;

            },


            handleDelete(index, row) {
                axios.get("http://localhost:8888/user/detele?id="+row.id).then(res=>{
                    if(res.data.status){
                        this.$message({
                            message: '恭喜你'+res.data.msg,
                            type: 'success'
                        });
                        this.form={};
                        this.show=false;
                        this.findALL();
                    }else {
                        this.$message.error(res.data.msg);
                    }
                });
            },

            onSubmit(userFrom) {
                this.$refs[userFrom].validate((valid) => {
                if (valid) {
                    axios.post("http://localhost:8888/user/save",this.form).then(res=>{
                        // this.tableData=res.data;
                        console.log(res.data);
                        if(res.data.status){
                            this.$message({
                                message: '恭喜你修改成功',
                                type: 'success'
                            });
                            this.form={};
                            this.show=false;
                            this.findALL();
                        }else {
                            this.$message.error(res.data.msg);
                        }
                    })
                } else {
                    this.$message.error("当前数据不合法");
                    return false;
                }
            });

                // console.log('submit!');

            },

            findALL(page,size){
                page = page ?page:this.pageNow;
                size = size ?size:this.size;
                axios.get("http://localhost:8888/user/findByPage?pageNow="+page+"&pageSize="+size).then(res=>{
                    this.tableData=res.data.users;
                    this.total=res.data.total;
                });
            }
        },
        created(){
           this.findALL();
        }

    }
</script>

<style scoped>
    .transition-box {
        margin-bottom: 10px;
        width: 100%;
        height: 400px;
        border-radius: 4px;
        text-align: center;
        padding: 40px 20px;
        box-sizing: border-box;
        margin-right: 20px;
    }
</style>